import React from 'react'
import css from './ProjectCell.css'

const ProjectCell = (props) => {
   return (
        <div className={css.ProjectCell}>
            <input className={css.ProjectName} type="text" placeholder="Project Name" />
            <input className={css.ProjectCost} type="number" placeholder="cost" />
        </div>
    )   
}

export default ProjectCell



