import React, { Component } from 'react'
import HistoryCell from './HistoryCell/HistoryCell';

import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

import axios from '../../network/axios-transactions';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import * as actions from '../../store/actions';
import Spinner from '../../components/Spinner/Spinner';
import Aux from '../../hoc/Aux/Aux';
import DateRangeCell from '../../components/DateRangeCell/DateRangeCell';


class History extends Component {

    componentDidMount() {
        this.props.onFetchTransactions(this.props.token, this.props.userId)
    }

    render() {
        let tranArr = <Spinner />
        if ( !this.props.loading ) {
            tranArr = this.props.transactions.map ( tran => {
                return <HistoryCell 
                key={tran.id} 
                tran={tran}
                />
            })
        }
        
        if (!this.props.token) {
            tranArr = <Redirect to="/auth"/>
        }

        return (
            <Aux>
                <DateRangeCell />
                {tranArr}
            </Aux>
        )
    }
}

const mapStateToProps = state => {
    return {
        transactions: state.transaction.transactions,
        loading: state.transaction.loading,
        token: state.auth.token,
        userId: state.auth.userId
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onFetchTransactions: (token, userId) => dispatch( actions.fetchTransactions(token, userId) ),
        // removed from use, as i'm trying to implement the idb  
        // onFetchTransactionsOffline: (token, userId) => dispatch( actions.fetchTransactionRequest(token, userId) )
    };
};

export default connect( mapStateToProps, mapDispatchToProps )( withErrorHandler( History, axios ) );


