import idb from 'idb'
const dbVersion = 2
const DB_NAME = 'TRANSACTIONS'

export const saveTransactions = async (trArr) => {
    dbPromise.then(db => {
        const tx = db.transaction(DB_NAME, 'readwrite');
        for (const item of trArr) {
            tx.objectStore(DB_NAME).put({
                ...item });
        }
        return tx.complete;
    });
}

