import * as actionTypes from './actionTypes';
import axios from '../../network/axios-transactions';
import * as idb from '../idb/transactions'

export const newTransactionSuccess = ( id, transactionData ) => {
    return {
        type: actionTypes.NEW_TRANSACTION_SUCCESS,
        transactionId: id,
        transactionData: transactionData,
    };
};

export const newTransactionFail = ( error ) => {
    return {
        type: actionTypes.NEW_TRANSACTION_FAIL,
        error: error
    };
}

export const newTransactionStart = () => {
    return {
        type: actionTypes.NEW_TRANSACTION_START
    };
};

export const newTransaction = ( transactionData, token ) => {
    return dispatch => {
        dispatch( newTransactionStart() );
        axios.post( '/addtransaction')
            .then( response => {
                dispatch( newTransactionSuccess( response.data.name, transactionData ) );
            } )
            .catch( error => {
                dispatch( newTransactionFail( error ) );
            } );
    };
};

export const newTransactionInit = () => {
    return {
        type: actionTypes.NEW_TRANSACTION_INIT
    };
};

export const fetchTransactionsSuccess = ( transactions, userId ) => {    
    return {
        type: actionTypes.FETCH_TRANSACTIONS_SUCCESS,
        transactions: transactions
    };
};

export const fetchTransactionsFail = ( error ) => {
    return {
        type: actionTypes.FETCH_TRANSACTIONS_FAIL,
        error: error
    };
};

export const fetchTransactionsStart = () => {
    return {
        type: actionTypes.FETCH_TRANSACTIONS_START
    };
};

export const fetchTransactions = (token, userId) => {
    return dispatch => {
        dispatch(fetchTransactionsStart());
        // const queryParams = '?auth=' + token + '&transactionBy="userId"&equalTo="' + userId + '"';
        axios.get( '/transactions')
            .then( res => {
                
                const fetchTransactions = [];
                for ( let key in res.data ) {
                    fetchTransactions.push( {
                        ...res.data[key],
                        id: key
                    } );
                }
                idb.saveTransactions(fetchTransactions)
                dispatch(fetchTransactionsSuccess(fetchTransactions, userId));
            } )
            .catch( err => {
                dispatch(fetchTransactionsFail(err));
            } );
    };
};

// this works but is saving all data in the localStorage
// i prefer indexDb
export const fetchTransactionRequest = (token, userId) => {
    return {
        type: actionTypes.FETCH_TRANSACTIONS_REQUEST,
        meta: { 
            offline: {
                effect: { url: '/transactions' },
                commit: { type: actionTypes.FETCH_PROJECTS_SUCCESS },
                rollBack: { type: actionTypes.FETCH_TRANSACTIONS_SUCCESS }
            }
        }
    }
}



